<template>
  <v-card>
    <v-card-title>
      <span class="headline">{{ title }}</span>
    </v-card-title>
    <v-card-text>
      <v-form v-model="valid" ref="form" lazy-validation>
        <v-row dense>
          <v-col cols="8">
            <v-text-field required v-model="formValues.name" label="Name" :rules="[rules.required]"></v-text-field>
          </v-col>
          <v-col cols="2">
            <v-switch v-model="formValues.enable" label="Enable"></v-switch>
          </v-col>
          <v-col cols="12">
            <v-autocomplete
              chips
              clearable
              small-chips
              label="Producer"
              item-value="uuid"
              item-text="name"
              :items="producers"
              deletable-chips
              v-model="formValues.producer_uuid"
            ></v-autocomplete>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
    <v-spacer></v-spacer>
    <v-card-actions class="align-end justify-end">
      <v-btn color="darken-1" text @click="onCancel"> Cancel </v-btn>
      <v-btn color="blue darken-1" :loading="isSaving" text @click="onSubmit(formValues)"> {{ saveTitle }} </v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
import _cloneDeep from 'lodash/cloneDeep';

export const initialFormValues = () => ({
  name: "consumer",
  enable: true,
  producer_uuid: null,
  stream_clone_uuid: null,
});
export default {
  name: 'flow-consumers-form',
  props: ['isEdit', 'onCancel', 'onSave', 'item', 'isSaving', 'streamId', 'producers'],
  data: () => ({
    rules: {
      required: value => !!value || 'Required.',
      numberRule: v => !isNaN(parseFloat(v)) || 'Required.',
    },
    valid: false,
    formValues: {
      ...initialFormValues(),
    },
  }),
  computed: {
    lowerCaseTitle() {
      return 'consumers';
    },
    titleCase() {
      return 'Consumers';
    },
    title() {
      return this.isEdit ? `Edit ${this.titleCase}` : `Create ${this.titleCase}`;
    },
    saveTitle() {
      return this.isEdit ? 'Save' : 'Create';
    },
  },
  mounted() {
    if (this.isEdit && this.item) {
      this.formValues = {
        ...this.formValues,
        ...this.item,
      };
    }

    if (!this.isEdit && this.streamId) {
      this.formValues = {
        ...this.formValues,
        stream_clone_uuid: this.streamId,
      };
    }
  },
  methods: {
    cleanUp() {
      this.formValues = {};
      if (typeof this.onCancel === 'function') this.onCancel();
    },
    onSubmit() {
      if (!this.$refs.form.validate()) {
        return;
      }
      if (typeof this.onSave !== 'function') {
        throw new Error(`${this.lowerCaseTitle} form onSave function not passed`);
      }
      return this.onSave(this.formValues)
        .then(() => {
          this.$toasted.show(`Successfully ${this.isEdit ? 'updated' : 'created'} ${this.lowerCaseTitle}.`);
          this.cleanUp(true);
        })
        .catch(err => {
          this.$toasted.show(`Failed to ${this.isEdit ? 'update' : 'create'} ${this.lowerCaseTitle}.`, {
            type: 'error',
          });
        });
    },
  },
};
</script>
