<script>
import StreamsForm from './streamsForm.vue';
import ROUTES from '../../../constants/routes';
import GenericGrid from '../../Common/GenericGrid.vue';
import IconButton from '../../Common/IconButton.vue';
import ProducersForm from '../Producers/producersForm.vue';
import ConsumersForm from '../Consumers/consumersForm.vue';
import { rfStreamService } from '../../../services/rfStreamsService';
import CommandGroupForm from '../CommandGroup/commandGroupForm.vue';
import { rfProducersService } from '../../../services/rfProducersService';
import { rfConsumersService } from '../../../services/rfConsumersService';
import { rfCommandGroupService } from '../../../services/rfCommandGroupService';

const dialogNames = {
  viewProducers: 'viewProducer',
  viewConsumers: 'viewConsumers',
  viewCommandGroup: 'viewCommandGroup',
  createProducers: 'createProducers',
  createConsumers: 'createConsumers',
  createCommandGroup: 'createCommandGroup',
  associateProducers: 'associateProducers',
  associateConsumers: 'associateConsumers',
  associateCommandGroup: 'associateCommandGroup',
};

const redirectMap = {
  [dialogNames.viewProducers]: streamId => ROUTES.rfProducers.replace(':streamId', streamId),
  [dialogNames.viewConsumers]: streamId => ROUTES.rfConsumers.replace(':streamId', streamId),
  [dialogNames.viewCommandGroup]: streamId => ROUTES.rfCommandGroups.replace(':streamId', streamId),
};
export default {
  components: {
    GenericGrid,
    StreamsForm,
    IconButton,
    ProducersForm,
    ConsumersForm,
    ConsumersForm,
    CommandGroupForm,
  },
  name: 'flowNetworkGrid',
  data() {
    return {
      selectedItem: {},
      producerValues: {},
      toolbarTitle: 'Streams',
      service: rfStreamService,
      isCreatingProducer: false,
      isCreatingConsumer: false,
      isCreatingCommandGroup: false,
      dialogNames: { ...dialogNames },
      dialog: {
        [dialogNames.createProducers]: false,
        [dialogNames.createConsumers]: false,
        [dialogNames.createCommandGroup]: false,
        [dialogNames.associateProducers]: false,
        [dialogNames.associateConsumers]: false,
        [dialogNames.associateCommandGroup]: false,
      },
      gridHeaders: [
        {
          text: 'Name',
          align: 'center',
          value: 'name',
          sortable: true,
        },
        {
          text: 'View',
          align: 'center',
          value: 'gridItemActions',
          sortable: false,
        },
        // {
        //   text: 'Associate Actions',
        //   align: 'center',
        //   value: 'gridItemActionsTwo',
        //   sortable: false,
        // },
        {
          text: 'Actions',
          align: 'center',
          value: 'actions',
          sortable: true,
        },
      ],
    };
  },
  computed: {
    flowNetworks() {
      return this.$store.state.rfFlowNetworks;
    },
  },
  methods: {
    toggleDialog(dialogName, item) {
      this.selectedItem = item;
      this.dialog[dialogName] = !this.dialog[dialogName];

      if (this.dialog[dialogName]) {
        this.selectedItem = item;
      } else {
        this.selectedItem = null;
      }
    },
    closeDialog(dialogName) {
      this.dialog[dialogName] = false;
      this.selectedItem = {};
    },
    createProducer(data) {
      this.isCreatingProducer = true;
      return rfProducersService
        .create(data)
        .then(data => {
          this.closeDialog(this.dialogNames.createProducers);
          return data;
        })
        .finally(() => {
          this.isCreatingProducer = false;
        });
    },
    createConsumer(data) {
      this.isCreatingConsumer = true;
      return rfConsumersService
        .create(data)
        .then(data => {
          this.closeDialog(this.dialogNames.createConsumers);
          return data;
        })
        .finally(() => {
          this.isCreatingConsumer = false;
        });
    },
    createCommandGroup(data) {
      this.isCreatingCommandGroup = true;
      return rfCommandGroupService
        .create(data)
        .then(data => {
          this.closeDialog(this.dialogNames.createCommandGroup);
          return data;
        })
        .finally(() => {
          this.isCreatingCommandGroup = false;
        });
    },
    redirectTo(viewName, item) {
      const streamId = item.uuid;
      if (!streamId) {
        return;
      }
      let destination = redirectMap[viewName];
      if (typeof destination === 'function') {
        this.$router.push({ path: destination(streamId) });
      }
    },
  },
};
</script>
