<template>
  <v-card>
    <v-card-title>
      <span class="headline">{{ title }}</span>
    </v-card-title>
    <v-card-text>
      <v-form v-model="valid" ref="form" lazy-validation>
        <v-row dense>
          <v-col cols="12">
            <v-text-field
              required
              v-model="formValues.name"
              label="Stream name"
              :rules="[rules.required]"
            ></v-text-field>
          </v-col>
          <v-col cols="12">
            <v-autocomplete
              chips
              multiple
              clearable
              small-chips
              label="Flow networks"
              return-object
              item-value="uuid"
              item-text="name"
              :items="flowNetworks"
              deletable-chips
              v-model="formValues.flow_networks"
            ></v-autocomplete>
          </v-col>
          <v-col cols="12">
            <v-switch v-model="formValues.is_remote" label="Enable Remote"></v-switch>
          </v-col>
          <v-col cols="12">
            <v-text-field required v-model="formValues.rubix_uuid" label="Rubix Point"></v-text-field>
          </v-col>
          <v-col cols="12">
            <v-text-field
              v-if="formValues.is_remote"
              required
              v-model="formValues.remote_rubix_uuid"
              label="Remote Rubix Point"
            ></v-text-field>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
    <v-spacer></v-spacer>
    <v-card-actions class="align-end justify-end">
      <v-btn color="darken-1" text @click="onCancel"> Cancel </v-btn>
      <v-btn color="blue darken-1" :loading="isSaving" text @click="onSubmit(formValues)"> {{ saveTitle }} </v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
export const initialFormValues = () => ({
  name: null,
  is_remote: true,
  rubix_uuid: null,
  remote_rubix_uuid: null,
});
export default {
  name: 'streams-form',
  props: ['isEdit', 'onCancel', 'onSave', 'item', 'isSaving', 'flowNetworks'],
  data: () => ({
    selectedFlowNetworks: [],
    rules: {
      required: value => !!value || 'Required.',
      numberRule: v => !isNaN(parseFloat(v)) || 'Required.',
    },
    valid: false,
    formValues: {
      ...initialFormValues(),
    },
  }),
  computed: {
    title() {
      return this.isEdit ? 'Edit Streams' : 'Create Streams';
    },
    saveTitle() {
      return this.isEdit ? 'Save' : 'Create';
    },
  },
  mounted() {
    if (this.isEdit && this.item) {
      this.formValues = {
        ...this.formValues,
        ...this.item,
      };
    }
  },
  methods: {
    cleanUp() {
      this.formValues = {};
      if (typeof this.onCancel === 'function') this.onCancel();
    },
    onSubmit() {
      if (!this.$refs.form.validate()) {
        return;
      }
      if (typeof this.onSave !== 'function') {
        throw new Error('Flow network form onSave function not passed');
      }
      return this.onSave(this.formValues)
        .then(() => {
          this.$toasted.show(`Successfully ${this.isEdit ? 'updated' : 'created'} streams`);
          this.cleanUp(true);
        })
        .catch(() => {
          this.$toasted.show(`Failed to ${this.isEdit ? 'update' : 'create'} streams`);
        });
    },
  },
};
</script>
